

input[type="button"] {
    appearance: none;
    background-color: transparent;
    border: 2px solid #022B3A ;
    border-radius: 15px;
    box-sizing: border-box;
    color: #3B3B3B;
    cursor: pointer;
    display: inline-block;
    font-family: Roobert,-apple-system,BlinkMacSystemFont,"Segoe UI",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";
    font-size: 16px;
    font-weight: 600;
    line-height: normal;
    
    min-height: 60px;
    min-width: 0;
    outline: none;
    padding: 16px 24px;
    text-align: center;
    text-decoration: none;
    transition: all 300ms cubic-bezier(.23, 1, 0.32, 1);
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    width: 100%;
    will-change: transform;

  }
  
input[type="button"]:hover {
    color: #fff;
    background-color: #022B3A ;
    box-shadow: rgba(0, 0, 0, 0.25) 0 8px 15px;
    transform: translateY(-2px);
  }
  
  .flex-form {
    
    width: 100%;
    box-shadow: 5px 8px 16px rgba(0, 0, 0, 0.3);
    border-radius: 14px;
   margin: 10px auto;
  
  }
  
  .flex-form > * {
    width: 100%;
    border: 0;
    padding: 0 0 0 10px;
    background: #fff;
    line-height: 50px;
    font-size: 1rem;
    border-radius: 14px;
    outline: 0;
    border:2px solid  #006589;
 
  
  
  }
  
  input[type="search"] {
    flex-basis: 500px;
  }

  .wrapper{

    display: flex;
    overflow: auto;
    width: 300px;
    width: fit-content;

    margin-top: 0;
    margin-bottom: 0;
    width: 100%;
    height: 60%;
    overflow-x: scroll;

    scrollbar-width: thin;
  }
  
  div.scrollmenu {
    background-color: #333;
    overflow: auto;
    white-space: nowrap;
  }
  
  div.scrollmenu a {
    display: inline-block;
    color: white;
    text-align: center;
    padding: 14px;
    text-decoration: none;
  }
  
 .but{
  margin-bottom: 30px;  
 }